<template>
    <header class="sticky-top border-bottom shadow-sm">
        <div class="w-100 px-3">

            <nav class="navbar navbar-expand-lg navbar-light bg-white py-0">

                <!-- the project name -->
                <a href="javascript:void(0);"
                   @click="linkTo('home')"
                   class="navbar-brand ml-3 mr-5 py-2"
                   v-text="currentProject.key"
                   v-show="isNotHome"></a>

                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav" v-show="isNotHome">
                        <li class="nav-item" :class="{active: isDemo}">
                            <a href="javascript:void(0);"
                               class="nav-link p-3"
                               @click="linkTo('demo')"><i18n path="common.demoAndDocument"></i18n></a>
                        </li>
                        <li class="nav-item" :class="{active: isChangeLog}">
                            <a href="javascript:void(0);"
                               class="nav-link p-3"
                               @click="linkTo('log')"><i18n path="common.changeLog"></i18n></a>
                        </li>
                    </ul>

                    <!-- projects and languages -->
                    <ul class="navbar-nav ml-auto" >
                        <li class="nav-item">
                            <a href="javascript:void(0);" class="nav-link py-3 px-1"
                               v-show="isNotHome"><i18n path="common.project"></i18n>：
                            </a>
                        </li>
                        <li class="nav-item" :class="{'nav-focus': prjFocus}">
                            <v-selectmenu :data="projectList"
                                          title="Vue plugins"
                                          key-field="key"
                                          show-field="key"
                                          @show="prjFocus = true"
                                          @hide="prjFocus = false"
                                          @values="projectChange"
                                          :scroll="false"
                                          :query="false"
                                          v-model="current">
                                <a href="javascript:void(0);" class="nav-link py-3 px-3">
                                    {{currentProject.key}} <fa-icon icon="caret-down"></fa-icon>
                                </a>
                            </v-selectmenu>
                        </li>
                        <li class="nav-item">
                            <a href="javascript:void(0);"
                               class="nav-link py-3 px-1" ><i18n path="common.language"></i18n>：
                            </a>
                        </li>
                        <li class="nav-item" :class="{'nav-focus': langFocus}">
                            <v-selectmenu :data="languages"
                                          :regular="true"
                                          @show="langFocus = true"
                                          @hide="langFocus = false">
                                <a href="javascript:void(0);" class="nav-link p-3">
                                    <i18n path="common.languageDesc"></i18n> <fa-icon icon="caret-down"></fa-icon>
                                </a>
                            </v-selectmenu>
                        </li>
                        <li class="nav-item">
                            <a :href="`https://github.com/TerryZ/${currentProject.key!=='Home'?currentProject.key:''}`"
                               class="nav-link p-3"
                               target="_blank">
                                <fa-icon :icon="['fab','github']" class="fa-lg"></fa-icon> view on <strong>Github</strong>
                            </a>
                        </li>
                    </ul>
                </div>
            </nav>

        </div>
    </header>
</template>

<script>
    import '@/styles/header.scss';
    import { urls,keys, getProject, getProjectByKey } from '@/store/urls';
	import changeLog from "@/store/changelog";
    const HOME = 'home', DEMO = 'demo', LOG = 'log';

    export default {
        name: 'Header',
        data(){
            return {
                current: null,
                projectList: urls,
                lang: this.$store.state.language,
                prjFocus: false,
                langFocus: false,
                languages: [
                    {content:'English',callback(){ this.lang = 'en'; }},
                    {content:'简体中文',callback(){ this.lang = 'cn'; }}
                ]
            };
        },
        methods:{
            projectChange(data){
            	//console.log(data)
                //this.$store.commit('currentProject', this.current);
            },
            linkTo(type){
                if(this.currentProject && Object.keys(this.currentProject).length) {
                	switch (type) {
                        case HOME:
                        	this.$router.push({ path: this.currentProject.home });
                        	break;
                        case DEMO:
							this.$router.push({ path: this.currentProject.demo });
                        	break;
                        case LOG:
							this.$router.push({ path: `/changelog/${this.currentProject.key}` });
                        	break;
					}
                }
            },
            prjBlur(){
                this.prjFocus = false;
            },
            langBlur(){
                this.langFocus = false;
            }
        },
        watch:{
            lang(){
                this.$store.commit('changeLanguage', this.lang);
            },
			current(val){
            	if(!val) return;
            	const project = getProjectByKey(val);
            	if(project && Object.keys(project).length) {
					this.$store.commit('currentProject', project);
					this.$router.push({ path: project.home }, ()=>{
					    window.scrollTo(0, 0);
                    });
                }
            }
        },
        computed: {
            isNotHome(){
            	const project = this.$store.state.currentProject;
            	if(!project || !Object.keys(project).length) return false;
                return project.key !== 'Home';
            },
            isDemo(){
            	return this.$store.state.isDemo;
            },
            isChangeLog(){
				return this.$store.state.isChangeLog;
            },
            currentProject(){
            	return this.$store.state.currentProject;
            }
        },
        beforeCreate(){
            const project = this.$store.state.currentProject;
            if(project && Object.keys(project).length) this.current = project.key;
        }
    }
</script>