import Vue from 'vue';
import Router from 'vue-router';
import { config } from './utils';

Vue.use(Router);

const routes = new Router({
    routes: [
        {path:'/',component: resolve=>require(['@/views/Home'],resolve)},
        {path:'/changelog/:project',component: resolve=>require(['@/views/ChangeLog'],resolve)},
        {path:'/dialog/',component: resolve=>require(['@/views/v-dialogs/Home'],resolve)},
        {path:'/dialog/demo',component: resolve=>require(['@/views/v-dialogs/Demo'],resolve)},
        {path:'/page',component: resolve=>require(['@/views/v-page/Home'],resolve)},
        {path:'/page/demo',component: resolve=>require(['@/views/v-page/Demo'],resolve)},
        {path:'/upload',component: resolve=>require(['@/views/v-uploader/Home'],resolve)},
        {path:'/upload/demo',component: resolve=>require(['@/views/v-uploader/Demo'],resolve)},
        {path:'/region',component:resolve=>require(['@/views/v-region/Home'],resolve)},
        {path:'/region/demo',component:resolve=>require(['@/views/v-region/Demo'],resolve)},
        {path:'/selectpage',component:resolve=>require(['@/views/v-selectpage/Home'],resolve)},
        {path:'/selectpage/demo',component:resolve=>require(['@/views/v-selectpage/Demo'],resolve)},
        {path:'/suggest',component:resolve=>require(['@/views/v-suggest/Home'],resolve)},
        {path:'/suggest/demo',component:resolve=>require(['@/views/v-suggest/Demo'],resolve)},
        {path:'/playback',component:resolve=>require(['@/views/v-playback/Home'],resolve)},
        {path:'/playback/demo',component:resolve=>require(['@/views/v-playback/Demo'],resolve)},
        {path:'/selectmenu',component:resolve=>require(['@/views/v-selectmenu/Home'],resolve)},
        {path:'/selectmenu/demo',component:resolve=>require(['@/views/v-selectmenu/Demo'],resolve)},
        {path:'/gallery',component:resolve=>require(['@/views/v-gallery/Home'],resolve)},
        {path:'/gallery/demo',component:resolve=>require(['@/views/v-gallery/Demo'],resolve)}
    ]
});

config(routes);

export default routes;