<template>
    <div class="show-code">
        <a href="javascript:void(0);" :class="{'btn-open':show}" @click="show=!show">
            {{showHideText}} <fa-icon icon="caret-down"></fa-icon>
        </a>
        <div v-show="show" class="code-container"><slot></slot></div>
    </div>
</template>

<script>
    import store from '@/store';
    export default {
        name: "showcode",
        data(){
            return {
                show: false,
                showHideText: store.state.i18n.common.showHideCode
            }
        }
    }
</script>

<style lang="scss" scoped>
div.show-code{
    display: block;
    margin: 5px 0 10px;
    position: relative;
    a{
        color: #aaa;
        padding: 5px 10px;
        display: inline-block;
        &:hover, &:visited, &:focus { color: #aaa; }
        & > .fa-caret-down { transition:transform .2s ease; }
    }

    .btn-open{
        border: 1px solid #ccc;
        border-radius: 4px;
        border-bottom: 0;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
        background-color: white;
        color: #333;
        &:hover, &:visited, &:focus { color: #333; }
        & > .fa-caret-down { transform: rotate(180deg); }
    }
    .code-container {
        border: 1px solid #ccc;
        border-radius: 4px;
        padding: 10px;
        border-top-left-radius: 0;
        margin-top: -1px;
        box-shadow: 0 3px 12px rgba(0,0,0,.15);
    }
}
</style>